@import '../../global-styles/mixins';

.projects {
    background:#000;
    color: white;
    // margin: 16px;
    position: relative;
    top: 60vh;


    @include desktop {
        padding: 0 12%;
    }



    &__heading {
        background: radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 50%);
        background-attachment: fixed;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 100;
        text-align: center;
        font-family: 'Oswald';
        font-size: 40px;

        @include tablet {
            background: radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 5%);
            background-attachment: fixed;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 100px;
            text-align: left;
        }
    }
}