@import '../../global-styles/mixins';

.nav {
    display: flex;
    justify-content: flex-end;
    width: 90%;
    font-family: 'Microsoft Yi Baiti';
    position: fixed;
    padding: 16px;
    // border-bottom: 1px solid #A94136;
  

    &__hamburger {
        display: flex;
        flex-direction: column;
        height: 18px;
        width: 30px;
        margin-left: auto;
        justify-content: space-between;

        @include tablet {
            display: none;
        }

        .nav__line {
            background-color: white;
            height: 4px;
            border-radius: 40px;
        }
    }
}