@import '../../global-styles/mixins';


.contact {
    position: relative;
    top: 60vh;
    color:white;
    padding-bottom: 10vh;

    @include desktop {
        padding: 0 12%;
    };

    &__heading {
        background: radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 50%);
        background-attachment: fixed;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 100;
        text-align: center;
        font-family: 'Oswald';
        font-size: 40px;
    }

    &__text{
        color: white;
        font-family: 'Roboto Slab'
    }

    &__label {
        background: radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 50%);
        background-attachment: fixed;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 100;
        font-family: 'Oswald';
        font-size: 20px;
        display: inline;
        margin-left: 12px;
    }

    &__link {
        margin: 16px 0;
        
    }

    &__img {
        width: 40px;
    }

    &__wrap {
        margin: 24px 0;
    }
}