@import '../../global-styles/mixins';

.project {
    color: #fff;
    background-color: #0b081b;
    padding: 4%;
    border-radius: 4px;


    
    &__border {
        background:radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 120%);
        background-attachment: fixed;
        padding: 1px;
        margin: 24px 0;
        border-radius: 4px;

        @include tablet {
            padding: 2px;
        }
    }

    a {
        
    }

    &__link {
        font-family: 'Oswald';
        font-weight: 900;
        text-decoration: none;
        color: rgb(255,158,0);
        margin: 0 12px;

        &:visited {
            color: rgba(158,0,255,1)
        }
    }

    &__npm {
        width: 40px
    }

    &__icon {
        width: 40px;
        height: auto;
        margin: auto 0 0 4px;

        &-inner {
            // width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
        }
    }

    &__inner {
        display:flex;
        flex-direction: column;
        justify-content: flex-end;
        width: 100%
    }

    &__icon-wrap {
        display:flex;
        flex-direction: column;
        justify-content: flex-end;

        align-self: flex-end;
        justify-self: flex-end;

        height: 100%;
        width: 100%;

        padding: 12px 0;

        margin: auto 0 0 4px;
    }

    &__title {
        font-family: Oswald;
        background: radial-gradient(circle, rgba(158,0,255,1) 0%, rgba(255,158,0,1) 70%);
        background-attachment: fixed;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 60px;
        font-weight: 600;
        margin: 2px 0;
    }

    &__img {
        display: block;
        width: 100%;
        @include tablet {
            width: 22%
        }
    }

    &__content {
        display: flex;
        flex-direction: column;

        @include tablet {
            flex-direction: row;
        }

    }

    &__text {
        width: 100%;
        padding: 8px 0;
        font-family: 'Roboto Slab';
        font-weight: 100;
        font-size: 20px;
        margin: 0;
        padding-left:8px;
    }
}

