@font-face {
    font-family: 'Roboto Slab';
    src: url(../assets/fonts/RobotoSlab.ttf);
    font-weight: normal;
};

@font-face {
    font-family: 'Raleway';
    src: url(../assets/fonts/Raleway.ttf);
    font-weight: lighter;
};

@font-face {
    font-family: 'Montserrat';
    src: url(../assets/fonts/Montserrat.ttf);
    font-weight: normal;
};
@font-face {
    font-family: 'Oswald';
    src: url(../assets/fonts/Oswald.ttf);
    font-weight: normal;
};

@mixin tablet(){
    @media screen and (min-width: 768px){
        @content
    }
};

@mixin desktop(){
    @media screen and (min-width: 1200px){
        @content
    }
}

